@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
* {
  font-family: "Open Sans", sans-serif;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  line-height: 1;
}

body {
  background: #E5E5E5;
}
.main__container{max-width:800px;margin:0 auto;padding:0 10px}.main__container .logo{text-align:center}.main__container .content{display:flex;justify-content:space-around}.main__container .content .left{flex-basis:130px}.main__container .content .right{flex-basis:500px}.main__container .content .error{margin-top:50px;text-align:center;display:flex;flex-direction:column}.main__container .content .error button{margin-top:50px;background:red;color:#fff;border:none;height:50px;cursor:pointer}

.filter__tickets{background:#FFFFFF;box-shadow:0px 2px 8px rgba(0,0,0,0.1);border-radius:5px;padding:20px}.filter__tickets h5{white-space:nowrap;font-weight:600;font-size:12px;color:#4A4A4A;letter-spacing:0.5px;text-transform:uppercase}.filter__tickets ul{list-style-type:none}.filter__tickets ul li{margin-top:20px}.filter__tickets ul li input{margin-right:5px;color:#4A4A4A;font-size:13px}

.tabs{display:flex;justify-content:space-between;border-radius:5px;border:1px solid #DFE5EC;height:50px;overflow:hidden}.tabs button{flex-basis:50%;background:#FFFFFF;border:none;display:flex;justify-content:center;align-items:center;letter-spacing:0.5px;font-weight:600;font-size:12px;color:#4A4A4A;text-transform:uppercase;cursor:pointer;border-radius:0}.tabs button.active{background:#2196F3;color:#fff}

.tiket__item{display:flex;flex-direction:column;background:#FFFFFF;box-shadow:0px 2px 8px rgba(0,0,0,0.1);border-radius:5px;margin-top:20px;padding:20px 50px 20px 20px}.tiket__item .tiket__item__price{display:flex;justify-content:space-between;align-items:center;margin-bottom:10px}.tiket__item .tiket__item__price span{color:#2196F3;font-weight:600;font-size:24px}.tiket__item .tiket__flight__info{margin-top:10px;display:flex;justify-content:space-between}.tiket__item .tiket__flight__info .tiket__flight__info__place,.tiket__item .tiket__flight__info .tiket__flight__info__way,.tiket__item .tiket__flight__info .tiket__flight__info__transfer{display:flex;justify-content:space-between;flex-direction:column;flex-basis:30%}.tiket__item .tiket__flight__info .tiket__flight__info__place span,.tiket__item .tiket__flight__info .tiket__flight__info__way span,.tiket__item .tiket__flight__info .tiket__flight__info__transfer span{text-transform:uppercase;color:#A0B0B9;font-weight:600;font-size:12px;margin-bottom:5px}.tiket__item .tiket__flight__info .tiket__flight__info__place time,.tiket__item .tiket__flight__info .tiket__flight__info__place p,.tiket__item .tiket__flight__info .tiket__flight__info__way time,.tiket__item .tiket__flight__info .tiket__flight__info__way p,.tiket__item .tiket__flight__info .tiket__flight__info__transfer time,.tiket__item .tiket__flight__info .tiket__flight__info__transfer p{color:#4A4A4A;font-weight:600;font-size:14px}

